import React from 'react';
import { Unity, useUnityContext } from "react-unity-webgl";


function Webgl () {

    const { unityProvider } = useUnityContext({
      loaderUrl: "./../assets/webgl/pasien1/Pasien1.loader.js",
      dataUrl: "./../assets/webgl/pasien1/Pasien1.data",
      frameworkUrl: "./../assets/webgl/pasien1/Pasien1.framework.js",
      codeUrl: "./../assets/webgl/pasien1/Pasien1.wasm",
    });
  
    return <Unity unityProvider={unityProvider} />;
}

export default Webgl;